.c-button {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 0 4vw 0;
}

.c-Tblog-button {
    width: 100%;
    font-size: 1rem;
    color: #2F9B9B;
    text-align: right;
    margin: 2.5rem 0 0;
    padding: 0 4vw;
    position: relative;
    right: 2.5vw;
    letter-spacing: .1vw;

    &::after {
        content: "";
        margin: auto;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 2vw;
        width: 10px;
        height: 10px;
        border-top: 1px solid #2F9B9B;
        border-right: 1px solid #2F9B9B;
        transform: rotate(45deg);
    }
}

.c-Tabout-button {
    width: 100%;
    color: #2F9B9B;
    font-size: 1rem;
    text-align: right;
    margin: 2rem 0 0;
    padding: 0 4vw;
    position: relative;
    right: 2.5vw;
    letter-spacing: .1vw;

    &::after {
        content: "";
        margin: auto;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 2vw;
        width: 10px;
        height: 10px;
        border-top: 1px solid #2F9B9B;
        border-right: 1px solid #2F9B9B;
        transform: rotate(45deg);
    }
}

.c-Tfacility-button {
    width: 100%;
    font-size: 1rem;
    color: #fff;
    position: relative;
    letter-spacing: .1vw;
    margin-top: 2vw;

    &::after {
        content: "";
        margin: auto;
        position: absolute;
        top: 0;
        bottom: 0;
        left: 7vw;
        width: 10px;
        height: 10px;
        transform: rotate(45deg);
    }
}
