caption {
  text-align: left;
}

td,
th {
  vertical-align: top;
  padding: 0;
}

th {
  text-align: left;
  font-weight: bold;
}

/* layout */
header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
}
header .l-header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1vw 4vw;
  transition: all 0.3s;
}
header .l-header__logo {
  width: 5%;
}
header .l-header__nav {
  width: 50%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  font-size: 1rem;
}
header .l-header__nav-list {
  margin: 0 0 0 1.5rem;
}

footer .l-footer {
  width: 100%;
  background: pink;
  color: white;
  text-align: center;
  padding: 2vw;
}

/* foundation */
.l-Tblog {
  width: 100%;
  height: 100%;
  position: relative;
}
.l-Tblog::after {
  content: "";
  position: absolute;
  top: 0;
  width: 85%;
  height: 100%;
  background: #fff;
  opacity: 0.2;
  right: 0;
  z-index: -1;
}
.l-Tblog::before {
  content: "";
  position: absolute;
  top: 1vw;
  width: 85%;
  height: 100%;
  background: #FFE8B7;
  opacity: 0.3;
  right: 1vw;
  z-index: -2;
}
.l-Tblog__wrap {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  margin: 0 0 0 auto;
  padding: 4vw 4vw 0;
  transition: all 0.3s;
}
.l-Tblog__wrap-ttl {
  font-size: 2.4vw;
  writing-mode: vertical-rl;
  margin: 0 5vw 0 0;
  position: relative;
  letter-spacing: 0.8%;
  color: #2F9B9B;
  border-radius: 5px;
  letter-spacing: 0.2rem;
}
.l-Tblog__wrap-ttl::after {
  content: "";
  width: 3px;
  height: 2vw;
  position: absolute;
  right: 1.5vw;
  top: 8vw;
  background: #2F9B9B;
  border-radius: 5px;
}
.l-Tblog__wrap__list {
  width: 20%;
  padding: 0 0 0 2vw;
}
.l-Tblog__wrap__list-img {
  width: 100%;
}
.l-Tblog__wrap__list__date {
  font-size: 0.8rem;
  margin-top: 0.8rem;
}
.l-Tblog__wrap__list__text {
  font-size: 0.8rem;
  margin-top: 0.8rem;
}

.l-Tabout {
  width: 100%;
  height: 100%;
  position: relative;
  margin: 10rem 0;
}
.l-Tabout::after {
  content: "";
  position: absolute;
  top: 0;
  width: 85%;
  height: 100%;
  background: #fff;
  opacity: 0.2;
  left: 0;
  z-index: -1;
}
.l-Tabout::before {
  content: "";
  position: absolute;
  top: 1vw;
  width: 85%;
  height: 100%;
  background: #FFE8B7;
  opacity: 0.3;
  left: 1vw;
  z-index: -2;
}
.l-Tabout__wrap {
  padding: 5vw 0 3vw 10vw;
  display: flex;
}
.l-Tabout__wrap-ttl {
  font-size: 2.4vw;
  writing-mode: vertical-rl;
  position: relative;
  letter-spacing: 0.8%;
  color: #2F9B9B;
  letter-spacing: 0.2rem;
}
.l-Tabout__wrap-ttl::after {
  content: "";
  width: 3px;
  height: 2vw;
  position: absolute;
  right: 1.3vw;
  top: 10vw;
  border-radius: 5px;
  background: #2F9B9B;
}
.l-Tabout__wrap-img {
  width: 40%;
}
.l-Tabout__wrap__textarea {
  font-size: 1rem;
  line-height: 2vw;
  letter-spacing: 0.3rem;
  margin: 3vw 0 0 5vw;
  width: 35%;
}
.l-Tabout__wrap__textarea-text :last-child {
  margin: 2rem 0 0 0;
}

.l-Tmenu-ttl {
  font-size: 2.4vw;
  color: #2F9B9B;
  padding: 0 0 2vw 10vw;
  letter-spacing: 0.2rem;
}
.l-Tmenu__slider {
  display: flex;
  position: relative;
}
.l-Tmenu__slider__img {
  padding: 0 0 0 10vw;
  width: 40%;
}
.l-Tmenu__slider__textarea {
  width: 45%;
  margin: 0 0 0 4vw;
  position: relative;
}
.l-Tmenu__slider__textarea-box {
  width: 20%;
  text-align: center;
  font-size: 1vw;
  padding: 1.2vw;
  color: #fff;
  background: #2F9B9B;
}
.l-Tmenu__slider__ttlwrap {
  font-size: 2vw;
  margin: 3.5vw 0 0 0;
}
.l-Tmenu__slider__ttlwrap-ttl {
  color: #2F9B9B;
  margin: 1vw 0 0 0;
}
.l-Tmenu__slider__ttlwrap-text {
  margin: 1vw 0 0 0;
  line-height: 2vw;
  font-size: 0.8vw;
}
.l-Tmenu__slider__detail {
  margin: 4.5vw 0 0 0;
  font-size: 1vw;
}
.l-Tmenu__slider__detail-ttl {
  letter-spacing: 0.2vw;
  color: #2F9B9B;
}
.l-Tmenu__slider__detail-text {
  margin-top: 0.4vw;
  letter-spacing: 0.2vw;
}
.l-Tmenu__slider-button {
  width: 30%;
  position: absolute;
  font-size: 0.8vw;
  bottom: 2vw;
  right: 6vw;
  text-align: center;
  padding: 0.8vw;
  border: 1px solid #2F9B9B;
  color: #2F9B9B;
}
.l-Tmenu .swiper {
  width: 100%;
  height: 100%;
}
.l-Tmenu .swiper-button-next::after {
  color: #2F9B9B;
  position: absolute;
  right: 2rem;
}
.l-Tmenu .swiper-button-prev::after {
  color: #2F9B9B;
  position: absolute;
  left: 2rem;
}

.l-Tfacility {
  margin-top: 10rem;
  position: relative;
}
.l-Tfacility__ttl {
  font-size: 2.4vw;
  color: #2F9B9B;
  padding: 0 0 0 10vw;
  letter-spacing: 0.2rem;
}
.l-Tfacility__wrap {
  width: 100%;
}
.l-Tfacility__textwrap {
  width: 40%;
  color: white;
  font-size: 0.8rem;
  padding: 3vw 0 3vw 10vw;
  margin-top: 15vw;
  position: relative;
}
.l-Tfacility__textwrap::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background: #2F9B9B;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.l-Tfacility__textwrap-ttl {
  letter-spacing: 0.4rem;
  font-size: 1.2vw;
}
.l-Tfacility__textwrap-text {
  width: 65%;
  font-size: 0.8vw;
  letter-spacing: 0.4rem;
  line-height: 1.5vw;
  margin: 1.2rem 0 0 0;
}
.l-Tfacility__imglist {
  width: 100%;
  position: absolute;
  margin: 6vw 0 0 0;
  right: 0;
  top: 0;
  display: flex;
  justify-content: flex-end;
}
.l-Tfacility__imglist-item {
  width: 31%;
  margin: 0 0 0 1rem;
}

.l-Taccess {
  width: 100%;
  height: 100%;
  background: #2F9B9B;
  margin-top: 15rem;
  padding: 0 0 5vw 0;
  position: relative;
}
.l-Taccess::after {
  content: "";
  background-image: url(/src/img/flower-object.png);
  width: 100px;
  background-size: 100%;
  height: 155px;
  position: absolute;
  top: -155px;
  left: 2%;
  z-index: 99;
}
.l-Taccess__ttl {
  width: 100%;
  font-size: 2.4vw;
  padding: 5vw 0 0 10vw;
  position: relative;
  color: #fff;
  letter-spacing: 0.4rem;
}
.l-Taccess__ttl::after {
  content: "";
  width: 4vw;
  height: 2px;
  position: absolute;
  left: 5vw;
  top: 6.5vw;
  background: #fff;
  border-radius: 5px;
}
.l-Taccess__wrap {
  display: flex;
  justify-content: space-between;
  margin: 3vw 0 0 10vw;
}
.l-Taccess__wrap__textgroup {
  width: 35%;
  height: 100%;
  font-size: 1vw;
  color: #fff;
}
.l-Taccess__wrap__textgroup__list {
  display: flex;
}
.l-Taccess__wrap__textgroup__list:not(:first-child) {
  margin: 3vw 0 0 0;
}
.l-Taccess__wrap__textgroup__list-ttl {
  width: 30%;
}
.l-Taccess__wrap__textgroup__list-impo {
  width: 100%;
}
.l-Taccess__wrap__textgroup__list-text {
  letter-spacing: 0.2rem;
  line-height: 2vw;
}
.l-Taccess__wrap-map {
  width: 60%;
}
.l-Taccess__wrap-map iframe {
  width: 70%;
  height: 100%;
}

.l-Tbooking {
  width: 100%;
  height: 100%;
  margin: 8vw 0;
}
.l-Tbooking__ttl {
  font-size: 2.4vw;
  text-align: center;
  color: #2F9B9B;
  letter-spacing: 0.8rem;
  position: relative;
}
.l-Tbooking__ttl p {
  font-size: 1.6vw;
  margin-top: 0.8vw;
  letter-spacing: 0.4rem;
}
.l-Tbooking__ttl::after {
  content: "";
  position: absolute;
  top: 8vw;
  left: 50%;
  transform: translate(-50%, 50%);
  width: 2px;
  height: 5vw;
  background: #2F9B9B;
}
.l-Tbooking__wrap {
  margin: 15vw 0 0 0;
}
.l-Tbooking__wrap__text {
  font-size: 1vw;
  text-align: center;
  color: #2F9B9B;
}
.l-Tbooking__wrap__text:not(:first-child) {
  margin: 1.2rem 0 0 0;
}
.l-Tbooking__wrap__box {
  margin: 2.5rem auto;
  position: relative;
  width: 35vw;
  height: 10vw;
}
.l-Tbooking__wrap__box::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 35vw;
  height: 10vw;
  background: rgba(47, 155, 155, 0.1058823529);
  z-index: -1;
}
.l-Tbooking__wrap__box-img {
  margin: 0 auto;
  width: 20%;
}
.l-Tbooking__wrap__box-text {
  font-size: 1.2vw;
  text-align: center;
  letter-spacing: 0.3rem;
  color: #2F9B9B;
}

/* foundation */
.l-hero {
  width: 100%;
  height: 100vh;
  position: relative;
}
.l-hero__wrap {
  position: absolute;
  top: 250px;
  left: 7%;
  padding: 0 4vw;
  transition: all 0.3s;
  z-index: 99;
}
.l-hero__wrap-ttl {
  margin-top: 1rem;
  font-size: 3.5rem;
  letter-spacing: 8px;
}
.l-hero__wrap-text {
  width: 60vw;
  margin-top: 2rem;
  font-size: 1rem;
  letter-spacing: 1vmin;
  line-height: 1.8rem;
}
.l-hero__wrap-logo {
  width: 100%;
}
.l-hero__wrap-logo img {
  width: 10rem;
}
.l-hero-img {
  height: 90vh;
  position: absolute;
  top: 100px;
  right: 0;
  padding: 0 4vw;
  transition: all 0.3s;
}
.l-hero-img img {
  width: 60vw;
  height: 80vmin;
  object-fit: cover;
}

/* object */
.c-button {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 0 4vw 0;
}

.c-Tblog-button {
  width: 100%;
  font-size: 1rem;
  color: #2F9B9B;
  text-align: right;
  margin: 2.5rem 0 0;
  padding: 0 4vw;
  position: relative;
  right: 2.5vw;
  letter-spacing: 0.1vw;
}
.c-Tblog-button::after {
  content: "";
  margin: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 2vw;
  width: 10px;
  height: 10px;
  border-top: 1px solid #2F9B9B;
  border-right: 1px solid #2F9B9B;
  transform: rotate(45deg);
}

.c-Tabout-button {
  width: 100%;
  color: #2F9B9B;
  font-size: 1rem;
  text-align: right;
  margin: 2rem 0 0;
  padding: 0 4vw;
  position: relative;
  right: 2.5vw;
  letter-spacing: 0.1vw;
}
.c-Tabout-button::after {
  content: "";
  margin: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 2vw;
  width: 10px;
  height: 10px;
  border-top: 1px solid #2F9B9B;
  border-right: 1px solid #2F9B9B;
  transform: rotate(45deg);
}

.c-Tfacility-button {
  width: 100%;
  font-size: 1rem;
  color: #fff;
  position: relative;
  letter-spacing: 0.1vw;
  margin-top: 2vw;
}
.c-Tfacility-button::after {
  content: "";
  margin: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 7vw;
  width: 10px;
  height: 10px;
  transform: rotate(45deg);
}

.u-margin {
  margin: 1vw 0;
}

.u-padding {
  padding: 0;
}